@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Black.woff2") format("woff2"), url("../fonts/Raleway-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Medium.woff2") format("woff2"), url("../fonts/Raleway-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  min-width: 360px;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Raleway", "Arial", sans-serif;
  background-color: #191312;
  color: white;
  font-size: 14px;
  line-height: auto;
  letter-spacing: 0.03em;
  font-weight: 600; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.container {
  max-width: 1304px;
  margin: 0 auto;
  padding-left: 32px;
  padding-right: 32px; }
  @media (max-width: 767px) {
    .container {
      padding-left: 16px;
      padding-right: 16px; } }

.scroll-lock {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-height: 100vh;
  overflow: hidden; }

.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-page {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-size: cover; }

.header {
  padding-top: 24px; }

.main-nav {
  display: flex; }

.main-nav__logo {
  margin-right: 19%; }

.main-nav__link-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  padding-top: 17px; }

.main-nav__link-item {
  margin-right: 49px; }
  .main-nav__link-item:last-child {
    margin-right: 0; }
  .main-nav__link-item a {
    text-transform: uppercase;
    color: #f0f0f0; }

.main-nav__contacts {
  display: flex;
  padding-top: 17px;
  margin-left: auto;
  margin-right: 7px; }

.main-nav__contacts-phone {
  color: #f0f0f0;
  font-feature-settings: 'pnum' on, 'lnum' on; }

.main-nav__contacts-city {
  position: relative;
  margin-right: 23px;
  padding-left: 24px;
  color: #f2890f;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 0.02em;
  line-height: 14px;
  text-transform: uppercase; }
  .main-nav__contacts-city::before {
    content: "";
    position: absolute;
    left: 0;
    top: -2px;
    width: 16px;
    height: 21px;
    background-image: url("../img/pointer.svg"); }
